@font-face {
  font-family: "montserrat";
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: "montserrat";
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "montserrat";
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal; }

html {
  box-sizing: border-box; }

body {
  display: flex;
  flex-direction: column;
  position: relative;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "montserrat", "Arial", sans-serif;
  font-weight: 400;
  color: #000000;
  font-style: normal;
  background-color: #ffffff;
  font-size: 20px;
  line-height: 24px; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  word-wrap: normal;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  background-color: #13477c;
  background-image: url(../img/first-screen-desktop.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; }

.page-header__container {
  max-width: 1200px;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
  display: flex;
  padding-top: 35px; }

.page-header__menu-toggle {
  display: none; }

.page-header__nav {
  flex-grow: 2;
  padding-top: 7px; }

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap; }

.nav__link {
  font-family: "montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
  color: #f9fbfd;
  margin-left: 36px; }
  .nav__link:hover {
    color: #adc8e4; }

.screen__wrap {
  max-width: 1200px;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
  padding-top: 192px;
  padding-bottom: 278px; }

.screen__header {
  font-family: "montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  color: #f9fbfd;
  margin: 0;
  padding-left: 95px;
  margin-bottom: 12px; }

.screen__text {
  font-family: "montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #f9fbfd;
  margin: 0;
  padding-left: 95px; }

.nav__close {
  display: none; }

@media (max-width: 1023px) {
  .page-header {
    background-image: url(../img/first-screen-tablet.jpg); }
  .page-header__container {
    max-width: 768px;
    padding: 0 45px;
    margin: 0 auto;
    box-sizing: border-box;
    display: flex;
    padding-top: 30px; }
  .page-header__nav {
    padding-top: 0; }
  .page-header__logo img {
    display: block;
    width: 38px;
    height: auto; }
  .nav__link {
    font-size: 14px;
    line-height: 17px;
    margin-left: 26px; }
  .nav__item {
    margin: 0;
    line-height: 17px; }
  .screen__wrap {
    max-width: 768px;
    padding: 0 45px;
    padding-top: 217px;
    padding-bottom: 276px; }
  .screen__header {
    font-size: 50px;
    line-height: 61px;
    padding-left: 0; }
  .screen__text {
    font-size: 18px;
    line-height: 22px;
    padding-left: 0; }
  .nav__close {
    display: none; } }

@media (max-width: 767px) {
  .page-header {
    background-image: url(../img/first-screen-mobile.jpg); }
  .page-header__container {
    min-width: 320px;
    padding: 0 15px;
    margin: 0 auto;
    box-sizing: border-box;
    display: flex;
    padding-top: 19px;
    justify-content: space-between; }
  .page-header__nav {
    display: none; }
  .page-header__logo img {
    width: 42px; }
  .page-header__nav--opened {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #ffffff;
    height: 100vh;
    width: 100vw; }
    .page-header__nav--opened .nav__list {
      flex-direction: column;
      padding-top: 89px; }
      .page-header__nav--opened .nav__list ::before {
        content: "";
        position: absolute;
        background-image: url("../img/logo-menu.svg");
        background-repeat: no-repeat;
        width: 42px;
        height: 22px;
        top: 20px;
        left: 16px; }
    .page-header__nav--opened .nav__item {
      margin-bottom: 21px; }
    .page-header__nav--opened .nav__link {
      font-size: 18px;
      line-height: 22px;
      margin-left: 15px;
      color: #011c40; }
    .page-header__nav--opened .nav__item--order {
      order: -1; }
  .nav__link {
    font-size: 18px;
    line-height: 66px;
    margin-left: 2px; }
  .nav__item--order {
    order: -1; }
  .page-header__menu-toggle {
    display: block;
    border: none;
    background-color: transparent;
    padding: 0;
    padding-top: 5px;
    cursor: pointer; }
  .page-header__menu-toggle-bar,
  .page-header__menu-toggle::after,
  .page-header__menu-toggle::before {
    content: "";
    display: block;
    width: 24px;
    height: 2px;
    margin-bottom: 4px;
    background-color: #f9fbfd; }
  .nav__close {
    display: block;
    border: none;
    background-color: transparent;
    padding: 0;
    padding-top: 5px;
    cursor: pointer;
    background-image: url("../img/close.png"); }
  .screen__header {
    font-size: 36px;
    line-height: 45px; }
  .screen__wrap {
    min-width: 320px;
    padding: 134px 15px 179px; }
  .screen__text {
    font-size: 16px;
    line-height: 22px; } }
